/* src/App.css */

/* Общие стили */
body {
  background-color: black;
  background: url("../public/Фон.png") repeat; /* Бесконечный фон */
  font-family: Impact;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.logo {
  padding: 20px;
  margin: 20px;
}

.participant-list,
.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  color: white;
}

.list-item {
  background: rgba(165, 165, 165, 0.6);
  padding: 10px;
  margin: 5px;
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
}

.header-items {
  padding: 10px;
  margin: 5px;
  border-radius: 10px;
  display: flex;
  justify-content: space-around;
}

a {
  margin-left: 5%;
  margin-right: 5%;
}

.header {
  display: flex;
  padding: 0;
}

/* Стили для ПК (большие экраны) */
@media screen and (min-width: 1025px) {
  .logo {
    height: 250px;
  }
  
  .list-item {
    width: 100%; /* Ширина списка */
    font-size: 50px; /* Размер шрифта для участников */
  }

  .header-text {
    font-size: 125px; /* Размер заголовка */
  }

  .header-items {
    width: 80%;
  }
}

/* Стили для планшетов */
@media screen and (min-width: 768px) and (max-width: 1024px) {
  .logo {
    height: 200px; /* Уменьшение логотипа для планшета */
  }

  .list-item {
    width: 100%; /* Увеличение ширины для планшета */
    font-size: 40px; /* Уменьшение шрифта для участников */
  }

  .header-text {
    font-size: 100px; /* Уменьшение размера заголовка */
  }

  .header-items {
    width: 85%; /* Более узкие элементы заголовка */
  }
}

/* Стили для смартфонов */
@media screen and (max-width: 767px) {
  .logo {
    height: 150px; /* Уменьшение логотипа для смартфонов */
  }

  .list-item {
    width: 90%; /* Широкие элементы для маленьких экранов */
    font-size: 30px; /* Уменьшение шрифта для участников */
  }

  .header-text {
    font-size: 60px; /* Уменьшение заголовка для смартфонов */
  }

  .header-items {
    width: 90%; /* Полная ширина заголовка для мобильных устройств */
  }

  a {
    margin-left: 2%;
    margin-right: 2%;
  }
}